<template>
<div>
	<header>
        <div class="container">
			<div class="intro-lead-in text-center" style="margin-top:7%;"></div>
            <div class="intro-text">
            </div>
			
        </div>
    </header>
	<div class="row">
		<div class="box" style="text-align:left;">
			<h2>Contact Us</h2>
			

			<el-form ref="form" :model="form" label-position="top" label-width="120px">
			  <el-form-item label="Phone Number">
				<el-input v-model="form.number"></el-input>
			  </el-form-item>
			  <el-form-item label="Email">
				<el-input v-model="form.email"></el-input>
			  </el-form-item>
			  <el-form-item label="Additional Information">
				<el-input type="textarea" v-model="form.additional"></el-input>
			  </el-form-item>
			  <el-form-item>
				<el-button type="primary" @click="sendEmail()">Send Message</el-button>
				<el-button>Cancel</el-button>
			  </el-form-item>
			</el-form>
		</div>

	</div>
</div>	
</template>
<style></style>
<script>
	import '../assets/js/smtp.js'
	export default{
		data(){
			return{
				form: {
					number:'',
					email:'',
					additional:''
				}
			}
		},
		methods:{
			sendEmail:function(){
				console.log("SUBMIT");
				Email.send("from@you.com",
					"to@them.com",
					"This is a subject",
					"this is the body",
					"smtp.yourisp.com",
					"username",
					"password");
			}
		}
	}
</script>


